.App {
  display: -ms-flexbox;
  display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
}

h1 {
	background-color: bisque;
	border: 2px solid crimson;
	border-radius: 20px;
	margin: 20px;
	padding: 20px;
}

.center {
	width: 90%;
	text-align: center;
	background-color: #F2F2F2;
}

button {
	cursor: pointer;
	padding: 10px;
	background-color: bisque;
	border: 2px solid crimson;
	border-radius: 20px;
	margin: 20px;
}

.cartBtn {
	position: absolute;
	right: 100px;
}

.logoutBtn {
	position: absolute;
	left: 100px;
}

table {
	border: 2px solid crimson;
	border-radius: 20px;
	padding: 20px;
	background-color: bisque;
	border-radius: 20px;
}



td, th {
	padding: 15px;
	margin: 15px;
}

.deleteIcon {
	width: 30px;
	cursor: pointer;
}

ul {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	list-style: none;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
}

.urlBox {
	font-size: 8px;
}

.edit {
	cursor: pointer;
	text-decoration: underline;
}

.historyList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	list-style: none;
}

.albumImg {
	width: 200px;
	border: 1px solid black;
}

.albumBox {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	border: 2px solid crimson;
	border-radius: 20px;
	margin: 20px;
	padding: 10px;
	background-color: bisque;
	cursor: pointer;
	width: 230px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

input {
	padding: 10px;
	border-radius: 10px;
}





